.solar_systm {
    position: relative;
    height: 100vh;
    width: 100vw;
    transform-style: preserve-3d;
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

.solar {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: black;
}

.menu {
    position: absolute;
    transform-origin: center center;
    border-radius: 50%;
}

.preview {
    width: 20px;
    height: 20px;
    background-color: yellow;
    border-radius: 50%;
    position: absolute;
}

.planet_description {
    position: absolute;
    text-align: center;
    color: white;
}

/* 행성 위치 조정 및 애니메이션 추가 */
.pluto {
    top: 50%;
    left: 50%;
    animation: rotatePluto 160s linear infinite;
    transform-origin: 300px 0px;
}

.neptune {
    top: 50%;
    left: 50%;
    animation: rotateNeptune 140s linear infinite;
    transform-origin: 250px 0px;
}

.uranus {
    top: 50%;
    left: 50%;
    animation: rotateUranus 120s linear infinite;
    transform-origin: 200px 0px;
}

.saturn {
    top: 50%;
    left: 50%;
    animation: rotateSaturn 100s linear infinite;
    transform-origin: 150px 0px;
}

.jupiter {
    top: 50%;
    left: 50%;
    animation: rotateJupiter 80s linear infinite;
    transform-origin: 120px 0px;
}

.mars {
    top: 50%;
    left: 50%;
    animation: rotateMars 60s linear infinite;
    transform-origin: 100px 0px;
}

.earth {
    top: 50%;
    left: 50%;
    animation: rotateEarth 40s linear infinite;
    transform-origin: 80px 0px;
}

.venus {
    top: 50%;
    left: 50%;
    animation: rotateVenus 30s linear infinite;
    transform-origin: 60px 0px;
}

.mercury {
    top: 50%;
    left: 50%;
    animation: rotateMercury 20s linear infinite;
    transform-origin: 40px 0px;
}

/* 행성 회전 애니메이션 */
@keyframes rotatePluto {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes rotateNeptune {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
